.main-favorite.favorite {
  overflow-x: scroll;
}

.category-button-wrap {
  gap: 12px;
  width: 100%;
  display: flex;
}

.category-button-button {
  color: var(--primary-text-color-03);
  border-color: rgba(5, 5, 5, .2);
  padding-left: 18px;
  padding-right: 18px;
  line-height: 1.28571;
}

.category-button-button.active {
  background-color: var(--main-color);
  color: var(--light-text-color);
  border: 1px solid var(--main-color);
  transition: all .3s;
}

.category-button-button:hover, .category-button-button:focus {
  background-color: var(--main-color);
}

.card-set {
  margin-top: 32px;
}

@media screen and (min-width: 768px) {
  .category-button-button {
    font-size: 16px;
    line-height: 1.25;
  }
}

@media screen and (min-width: 1280px) {
  .fav-set {
    flex-basis: calc((100% - var(--ident) * (4 - 1)) / 4);
  }
}

.no-recipe-icon {
  justify-content: center;
  margin-bottom: 24px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .no-recipe-icon {
    margin-bottom: 32px;
  }

  .fav-set {
    flex-basis: calc((100% - var(--ident) * (3 - 1)) / 3);
  }

  .main-favorite.favorite {
    padding-top: 64px;
  }

  .no-recipes {
    text-align: center;
    margin: 0 auto;
    font-size: 14px;
    line-height: 18px;
  }

  @media screen and (min-width: 768px) {
    .no-recipes {
      width: 481px;
      font-size: 16px;
      line-height: 24px;
    }
  }

  .visually-hidden {
    white-space: nowrap;
    -webkit-clip-path: inset(100%);
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    border: 0;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    position: absolute;
    overflow: hidden;
  }
}

